<script>
  import { createNamespacedHelpers } from "vuex";
  const { mapState } = createNamespacedHelpers("boxOfficeModule");
  export default {
    name:'boxOffice',
    
    data() {
      return {
        activeName:''
      };
    },

    computed:{
        ...mapState(['navData','boxOfficeData','boxOfficeList'])
    },

    created(){
      this.activeName = this.$route.name; //纠正头部导航栏激活状态
      
      //开启加载提示
      this.$toast.loading({
        duration: 0,
        message: '加载中...'
      });

      this.axios({
          method: 'GET',
          url: 'https://douban.uieee.com/v2/movie/weekly?apikey=0df993c66c0c636e29ecbb5344252a4a'
      }).then(result => {
          // console.log('result.data.subjects ==> ', result.data.subjects);
          this.$store.commit('boxOfficeModule/getBoxOfficeData',result.data.subjects);

          this.$toast.clear();

      }).catch(err => {
          // console.log('err ==> ', err);
          this.$toast.clear();

      })
    },

    methods:{
      // 返回上一步
      back() {
        this.$router.push({name:'home'});
      },

      //查看电影详情
      viewMovieDetail(o) {
        this.$router.push(o);
      },

      getUSboxOfficeData(){

      }
    }
  }
</script>

